import React, { Component } from 'react'
import './UserDialog.css'
import {signUp,signIn,sendPasswordResetEmail} from './leanCloud'

export default class UserDialog extends Component{
    constructor(props){
        super(props)
        this.state = {
            selected: 'signUp',
            selectedTab:'signInOrSignUp',
            formData:{
                email:'',
                username:'',
                password:''
            }
        }
    }
    swith(e){
        this.setState({
            selected:e.target.value
        })
    }
    signUp(e){
        e.preventDefault()
        let {email,username,password} = this.state.formData
        let success= (user)=>{
            console.log(user)
            this.props.onSignUp.call(null,user)
        }
        let error = (error)=>{
            switch(error.code){
                case 202:
                    alert('用户名已被占用')
                    break
                default:
                    alert(error)
                    break                    
            }
        }
        signUp(email,username,password,success,error)
    }
    signIn(e){
        e.preventDefault()
        let {username,password} = this.state.formData
        let success= (user)=>{
            console.log(user)
            this.props.onSignIn.call(null,user)
        }
        let error = (error)=>{
            switch(error.code){
                case 210:
                    alert('用户名与密码不匹配')
                    break
                default:
                    alert(error)
                    break                    
            }
        }
        signIn(username,password,success,error)
    }
    changeFormData(key,e){
        let stateCopy = JSON.parse(JSON.stringify(this.state))
        stateCopy.formData[key] = e.target.value
        this.setState(stateCopy)
    }
    render(){
        let signUpForm = (
            <form className="signUp" onSubmit={this.signUp.bind(this)}>
                <div className="row">
                    <lable>邮箱</lable>
                    <input type="email" className="email"
                        value={this.state.formData.email} 
                        onChange={this.changeFormData.bind(this,'email')} />
                </div>
                <div className="row">
                    <lable>用户名</lable>
                    <input type="text" value={this.state.formData.username} 
                        onChange={this.changeFormData.bind(this,'username')} />
                </div>
                <div className="row">
                    <lable>密码</lable>
                    <input type="password" className="password"
                        value={this.state.formData.password}
                        onChange={this.changeFormData.bind(this,'password')} />
                </div>
                <div className="row actions">
                    <button type="submit">注册</button>
                </div>
            </form>
        )
        let signInForm = (
            <form className="signIn" onSubmit={this.signIn.bind(this)}>
                <div className="row">
                    <lable>用户名</lable>
                    <input type="text" value={this.state.formData.username}
                        onChange={this.changeFormData.bind(this,'username')} />
                </div>
                <div className="row">
                    <lable>密码</lable>
                    <input type="password" className="password"
                        value={this.state.formData.password}
                        onChange={this.changeFormData.bind(this,'password')} />
                </div>
                <div className="row actions">
                    <button type="submit">登录</button>
                    <a href="javascript:;" onClick={this.showForgotPassword.bind(this)}>忘记密码了？</a>
                </div>
            </form>
        )
        let signInOrSignUp = (
            <div className="signInOrSignUp">
                <nav>
                    <lable>
                        <input type="radio" value="signUp" 
                            checked={this.state.selected === 'signUp'} 
                            onChange={this.swith.bind(this)}/> 注册
                    </lable> 
                    <label>
                        <input type="radio" value="signIn" 
                            checked={this.state.selected === 'signIn'} 
                            onChange={this.swith.bind(this)}/> 登录
                    </label> 
                </nav>
                <div className="panes">
                    {this.state.selected === 'signUp' ? signUpForm : null}
                    {this.state.selected === 'signIn' ? signInForm : null}
                </div>
            </div>
        )
        let forgotPassword = (
           <div className="forgotPassword">
               <h3>
                   重置密码
               </h3>
               <form className="forgotPassword" onSubmit={this.resetPassword.bind(this)}>
                   <div className="row">
                    <lable>邮箱</lable>
                    <input type="text" className="email"
                        value={this.state.formData.email}
                        onChange={this.changeFormData.bind(this,'email')} />
                </div>
                <div className="row actions">
                    <button type="submit">发送重置邮箱</button>
                    <a href="#" onClick={this.returnToSignIn.bind(this)}>返回登录</a>
                </div>
               </form>
           </div>
        )
        return (
            <div className="UserDialog-Wrapper">
                <div className="UserDialog">
                    {this.state.selectedTab === 'signInOrSignUp' ? signInOrSignUp : forgotPassword}
                </div>
            </div>
        )
    }
    showForgotPassword(){
        let stateCopy = JSON.parse(JSON.stringify(this.state))
        stateCopy.selectedTab = 'forgotPassword'
        this.setState(stateCopy)
    }
    returnToSignIn(){
        let stateCopy = JSON.parse(JSON.stringify(this.state))
        stateCopy.selectedTab = 'signInOrSignUp'
        this.setState(stateCopy)
    }
    resetPassword(e){
        e.preventDefault()
        sendPasswordResetEmail(this.state.formData.email)
    }
}


